import { useEffect, useState } from 'react';
import PropTypes from 'prop-types';
import React from 'react';
import './dropdown.css';

const Dropdown = ({ options, onSelect}) => {
  // tat pour suivre si le menu droulant est ouvert ou ferm
  const [isOpen, setIsOpen] = useState(false); 
  // tat pour stocker l'option slectionne
  const [selectedOption, setSelectedOption] = useState(null);
  
   // Slectionner automatiquement la premire option
   useEffect(() => {
    if (options.length > 0 && !selectedOption) {
      setSelectedOption(options[0]); // Slectionner la premire option par dfaut
      onSelect(options[0]); // Notifier le parent de la slection
    }
  }, [options, selectedOption, onSelect]);
  


  const handleToggle = () => {
    setIsOpen(!isOpen);// Inverser l'tat d'ouverture (ouvrir si ferm, fermer si ouvert)
  };

  const handleSelect = (option) => {
    setSelectedOption(option); // Met  jour l'option slectionne
    setIsOpen(false); // Ferme la liste aprs slection
    onSelect(option); // Appeler la fonction  passe en prop pour informer le parent de la slection
  };

  return (
    <div className="dropdown-container">
    
    <div className="dropdown" onClick={handleToggle}>
      <div className="dropdown-selected">
        {selectedOption && selectedOption.name}
      </div>
      <span className={`dropdown-icon ${isOpen ? 'open' : ''}`}>&#9662;</span>
    </div>

    {isOpen && (
      <ul className="dropdown-menu">
        {options.map((option, index) => (
          <li key={index} onClick={() => handleSelect(option)}>
            {option.name}
          </li>
        ))}
      </ul>
    )}

  </div>
);
};

Dropdown.propTypes = {
  options: PropTypes.arrayOf(PropTypes.any).isRequired, // options doit tre un tableau de chanes de caractres
  onSelect: PropTypes.func.isRequired,                    // onSelect doit tre une fonction
};

export default Dropdown;
