import { IEmployee } from "../models/IEmployee";

export class storageManagement {

    storage_key :string = "HRnet-Employees-List";

    addEmployee = (employee: IEmployee): any => {
        const storedEmployees = localStorage.getItem(this.storage_key);
    
        // Vrifier si des employs existent dj dans localStorage
        if (storedEmployees) {
            try {
                // Rcuprer et parser les employs existants
                const Employees = JSON.parse(storedEmployees) as IEmployee[] || [];
    
                // S'assurer que "Employees" est bien un tableau
                if (Array.isArray(Employees)) {
                    // Ajouter le nouvel employ au tableau sans craser les anciens
                    Employees.push(employee);
                    localStorage.setItem(this.storage_key, JSON.stringify(Employees));
                } else {
                    // Si pour une raison quelconque "Employees" n'est pas un tableau, on le rinitialise
                    localStorage.setItem(this.storage_key, JSON.stringify([employee]));
                }
            } catch (error) {
                console.error("Erreur lors de la rcupration ou du parsing des employs:", error);
                // Si une erreur survient, rinitialiser avec un tableau contenant le nouvel employ
                localStorage.setItem(this.storage_key, JSON.stringify([employee]));
            }
        } else {
            // Si aucun employ n'existe, crer un tableau avec le premier employ
            localStorage.setItem(this.storage_key, JSON.stringify([employee]));
        }
    }
    
    
     getEmployeeList = () : IEmployee[] =>{
       return JSON.parse (localStorage.getItem(this.storage_key)) as IEmployee[]
    }

}